.contactUs {
    margin: 3%;
    display: flex;
}

.contactUs-head {
    margin-left: 5%;
    font-size: clamp(1rem, 3vw, 2rem);
    flex: 1;
    font-weight: bold;
}

.contactUs-body {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-family: 'Quicksand', sans-serif;
    font-size:  clamp(0.75rem, 2vw, 1.25rem);
}